import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';

import { RecepieView } from '../recepie/recepie';
import { AddRecepie } from '../addRecepie/add';

import { ToastController } from 'ionic-angular';

// Data Models
import { Recepie } from '../../models/recepieModel';

// Services
import { MainService } from '../../services/main.service';


enum Categories {
	Dessert ,
	Lunch ,
	Dinner ,
}

@Component({
  	selector: 'page-home',
  	templateUrl: 'home.html'
})


export class HomePage {

	shouldShowCancel:boolean = false;
	searchText:string;

	public categories = Categories;
	public recepies:Recepie[];

	constructor(	
		public navCtrl: NavController, 
		private toastCtrl: ToastController,
		private _mainSrv: MainService
	) {
	
	}

	ngOnInit(){
		this._mainSrv.recepies$.subscribe((data)=>console.log(data))
		// console.log(this._mainSrv.recepies$)
	}
	onCancel() {

	}

	onInput(event) {

	}

	showCancelButton(event) {

	}

	changePage(page, data){
		switch(page) {
			case 'recepie-view':
				// this.store.dispatch(new AppActions.SetSelected(data));
				this.navCtrl.push(RecepieView)
				break;
			case 'add-recepie':
				this.navCtrl.push(AddRecepie)
				break;
		}	
	}

	removeRecepie(recepie){
		// this.store.dispatch(new RecepieActions.RemoveRecepie(recepie.id));
		let toast = this.toastCtrl.create({
			message: `${recepie.title} was deleted`,
			duration: 3000,
			position: 'bottom'
		});
		toast.present();
	}

}



